
import { Options, Vue } from "vue-class-component";

@Options({
  props: {
    msg: String,
  },
})
export default class HelloWorld extends Vue {
  msg!: string;
  private age = 13;
  get name(): string {
    return "leev";
  }
  get list(): [] {
    return JSON.parse(localStorage.getItem("list") || "[]");
  }
}
